___CSS_LOADER_EXPORT___.push([module.id, ".toast-container{max-width:350px}", "",{"version":3,"sources":["webpack://./resources/js/components/toast.vue"],"names":[],"mappings":"AAkDA,iBACA,eACA","sourcesContent":["<template>\n  <div class=\"toast-container position-fixed bottom-0 end-0 p-3\" style=\"z-index: 11\">\n    <div\n      v-for=\"(toast, index) in toasts\"\n      :key=\"index\"\n      class=\"toast  show  animate__animated animate__fadeIn\"\n      role=\"alert\"\n      aria-live=\"assertive\"\n      aria-atomic=\"true\"\n    >\n      <div class=\"toast-header\">\n        <strong class=\"me-auto\">{{ toast.title }}</strong>\n        <button\n          type=\"button\"\n          class=\"btn-close\"\n          @click=\"removeToast(index)\"\n          aria-label=\"Close\"\n        ></button>\n      </div>\n      <div class=\"toast-body\">\n        {{ toast.message }}\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"Toast\",\n  data() {\n    return {\n      toasts: [],\n    };\n  },\n  methods: {\n    addToast(title, message, time) {\n      const toast = { title, message };\n      this.toasts.push(toast);\n      setTimeout(() => {\n        this.toasts.splice(this.toasts.indexOf(toast), 1);\n      }, time);\n    },\n    removeToast(index) {   \n      this.toasts.splice(index, 1);\n    },\n  },\n};\n</script>\n\n<style>\n.toast-container {\n  max-width: 350px;\n}\n</style>\n"],"sourceRoot":""}]);
